import ErmjMainUI from "../panel/ErmjMainUI";
import ErmjContext from "../data/ErmjContext";
import ErmjDefine from "../data/ErmjDefine";
import ErmjPathHelper from "../data/ErmjPathHelper";
import ErmjDriver from "../ErmjDriver";

export default class ErmjBaseHandler
{    
    protected mainUI: ErmjMainUI;
    protected context: ErmjContext;
    protected Define: ErmjDefine;
    protected SitHelper: PVPSitHelper;
    protected Path: ErmjPathHelper;
    //判断协议是否需要进入队列
    public checkInQueue(msgParam)
    {
        return true;
    }
    
    //协议处理入口
    public Handle(msgParam)
    {
        this.mainUI = ErmjDriver.instance.mainUI;
        this.context = ErmjDriver.instance.Context;
        this.Define = ErmjDriver.instance.Define;
        this.SitHelper = ErmjDriver.instance.SitHelper;
        this.Path = ErmjDriver.instance.Path;
        this.refreshData(msgParam);
        //重连执行executeSync 正常执行execute
        if(this.context.syncMode)
            this.executeSync(msgParam)
        else
            this.execute(msgParam);
    }
  
      //数据刷新入口
      protected refreshData(msgParam)
      {}
  
      //正常流程逻辑处理
      protected execute(msgParam)
      {}
  
      //重连流程逻辑处理
      //默认为execute  如果涉及到动画   需要重写
      protected executeSync(msgParam)
      {
          this.execute(msgParam);
      }
}